import * as React from 'react';
import {Stack, StackProps} from "@mui/material";


export const WebPageContainer = ({sx, ...rest}: StackProps) => {
  return <Stack direction={"column"} sx={{
    marginTop: 4,
    marginBottom: 8,
    marginX: {
      xs: 0,
      sm: 2,
      md: 6,
      lg: 24,
      xl: 48
    },
    ...sx
  }}
                {...rest}>
  </Stack>
};