import {useControls} from "leva";
import {MMDLoader} from "three/examples/jsm/loaders/MMDLoader";
import {print} from "../../../../api/util/Logging";
import Fab from '@mui/material/Fab';
import {MMDAnimationHelper} from "three/examples/jsm/animation/MMDAnimationHelper";
import PauseIcon from '@mui/icons-material/Pause';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import * as THREE from 'three';
import {AnimationClip, AudioLoader} from 'three';
import {Typography} from "../../../../component/wrapper/MuiWrappers";
import {isMobile} from "react-device-detect";
import {getObjectId} from "../../../../api/util/StringUtil";
import {WebPageContainer} from "../../../../component/container/WebPageContainer";
import FeedbackWidget from "../../component/FeedbackWidget";
// import expa
// import {FilePicker} from "@mui/material";
const widgetId = getObjectId(302);
type Props = {};
const Cube = ({position, size, color}: any) => {
  const ref = useRef<any>();
  // delta is the time between frames, in seconds
  const random_seed = Math.random();
  useFrame((state, delta) => {
    if (ref.current) {
      ref.current.rotation.x += random_seed * delta * 2;
      ref.current.rotation.y += random_seed * delta * 1.5;
      ref.current.rotation.z += delta * 2;
      ref.current.position.x = Math.sin(2 * random_seed * state.clock.getElapsedTime() + random_seed) * 2;
      ref.current.position.z = Math.sin(state.clock.getElapsedTime() + random_seed) * 2;
    }
  });

  return <mesh ref={ref} position={position}>
    <boxGeometry args={size}/>
    {/*<MeshWobbleMaterial color={color} speed={1} factor={0.6}/>*/}
    <meshStandardMaterial color={color}/>
  </mesh>;
}

const Sphere = ({position, size, color}: any) => {
  const [isHovered, setIsHovered] = React.useState(false);
  const ref = useRef<any>();
  // delta is the time between frames, in seconds
  const random_seed = Math.random();
  useFrame((state, delta) => {
    if (isHovered) {
      return;
    }
    if (ref.current) {

      ref.current.position.x = Math.sin(state.clock.getElapsedTime() + random_seed * 10) * 2 * random_seed;
      ref.current.position.z = Math.sin(state.clock.getElapsedTime()) * 4 * random_seed;
    }
  });

  return <mesh ref={ref} position={position} onPointerEnter={(event) => {
    setIsHovered(true);
    event.stopPropagation();
  }} onPointerLeave={(event) => {
    setIsHovered(false);
    event.stopPropagation();
  }}>
    <sphereGeometry args={size}/>
    <meshStandardMaterial color={color} wireframe={true}/>
  </mesh>;
}


const MMDModelAnimation = ({modelUrl = "", vmdUrl = "", start = false}) => {
  const modelRef = useRef(); // Reference for the model
  const helperRef = useRef<MMDAnimationHelper | null>(null); // Properly typed reference for the animation helper
  // const phyicsRef = useRef<MMDPhysics | null>(null);
  // const [physicsApi] = useBox(() => ({ mass: 1, position: [0, 0, 0] }));

  // Load the PMX model using MMDLoader
  const model = useLoader(MMDLoader, modelUrl);

  useEffect(() => {
    if (!model || !vmdUrl) return;

    // Create a new MMDAnimationHelper instance
    const helper = new MMDAnimationHelper();
    helperRef.current = helper; // Assign helper to the ref

    // Load the VMD file and apply it to the model
    const loader = new MMDLoader();
    loader.loadAnimation(vmdUrl, model, (vmd) => {
      helper.add(model, {animation: vmd as AnimationClip, physics: false});
    }, void 0, (error) => {
      print("Error loading VMD file", error);

    });

    // model.traverse((child) => {
    //   if (child instanceof Mesh) {
    //     // Apply physics to each mesh part using physicsApi
    //     useBox({ mass: 1, position: [0, 0, 0] }); // Basic example
    //   }
    // });

    // Clean up the helper when the component unmounts
    return () => {
      // helper.dispose();
      helperRef.current = null; // Clean up the reference
      // phyicsRef.current = null;
    };
  }, [model, vmdUrl]);

  // Use the helper to update animations on each frame
  useFrame((_, delta) => {
    if (helperRef.current && start) {
      helperRef.current.update(delta);
    }
  });

  return <primitive ref={modelRef} object={model} dispose={null} scale={[0.25, 0.25, 0.25]}
                    position={[0, -3, 0]}/>;
};

const Scene = ({start}: { start: boolean }) => {
  const directionalLight = useRef<any>();
  const point1 = useRef<any>();
  const point2 = useRef<any>();
  const {lightColor, lightIntensity, lightPosition} = useControls({
    lightColor: {value: "white", label: "Light Color"},
    lightIntensity: {value: 1, label: "Light Intensity", min: 0, max: 10},
    lightPosition: {value: [0, 0, 2], label: "Light Position"}
  })

  const ambientLightRef = useRef<any>();
  const {ambientLightColor, ambientLightIntensity} = useControls({
    ambientLightColor: {value: "white", label: "Ambient Light Color"},
    ambientLightIntensity: {value: 0.4, label: "Ambient Light Intensity", min: 0, max: 10}
  });

  // useHelper(directionalLight, DirectionalLightHelper, 1, "red");
  // useHelper(point1, PointLightHelper, 1, "red");
  // useHelper(point2, PointLightHelper, 1, "red");
  return <>

    <pointLight ref={point1} color={"purple"} intensity={60} position={[-3, 2, -3]}/>
    <pointLight ref={point2} color={"blue"} intensity={30} position={[3, 2, -1]}/>
    <directionalLight position={lightPosition} ref={directionalLight} color={lightColor}
                      intensity={lightIntensity}/>
    <ambientLight intensity={ambientLightIntensity} color={ambientLightColor}
                  ref={ambientLightRef}/>
    {/*<MmdModel url={"/mmd/原神-刻晴/刻晴.pmx"}/>*/}
    {/*<Physics gravity={[0, 0, 0]} colliders={"ball"}>*/}
    {/*<CameraFollower condition={true}>*/}
    <MMDModelAnimation modelUrl={"/mmd/原神-刻晴/刻晴fix.pmx"}
                       vmdUrl={isMobile ? "/mmd/motion/jimingyue-motion.vmd" : "/mmd/motion/testB.vmd"}
                       start={start}/>
    {/*</CameraFollower>*/}
    {/*<MmdModelWithAnimation url={"/mmd/tda/Tda式初音ミクV4X_Ver1.00.pmx"}/>*/}
    {/*</Physics>*/}


    {/*<group position={[0, -1, 0]}>*/}
    {/*  <Cube position={[1, 1, 1]} size={[1, 1, 1]} color={"blue"}/>*/}
    {/*  <Sphere position={[-1, -1, -1]} size={[1, 30, 30]} color={"orange"}/>*/}
    {/*  <Cube position={[-1, 1, 1]} size={[1, 1, 1]} color={"green"}/>*/}
    {/*  <Sphere position={[1, -1, -1]} size={[1, 30, 30]} color={"yellow"}/>*/}
    {/*</group>*/}
    <OrbitControls/>
  </>;
}


const ThreeModel = (props: Props) => {

  const [audio, setAudio] = useState<any>(null);
  const audioRef = useRef(null);

  // Load the audio using useLoader
  const audioData = useLoader(AudioLoader, '/mmd/music/寄明月.mp3');
  const [start, setStart] = useState(false);

  useEffect(() => {
    if (audioData) {
      const sound = new THREE.Audio(new THREE.AudioListener());
      sound.setBuffer(audioData);
      sound.setLoop(true);
      sound.setVolume(0.5);
      setAudio(sound as any);
    }
  }, [audioData]);

  useEffect(() => {
    if (audio) {
      audioRef.current = audio;
      // audio.play();
    }
    return () => {
      if (audio) {
        audio.stop();
      }
    }
  }, [audio]);

  const handlePlay = () => {
    if (!audio) {
      return;
    }
    if (start) {
      setStart(false);
      audio.pause();
    } else {
      setStart(true);
      audio.play();
    }
  };

  return <>
    <Canvas className={"threeD"}
            style={{backgroundColor: "black", height: "100svh", width: "100wvh"}}>
      <Scene start={start}/>

    </Canvas>

    <Fab color="primary" aria-label="add" onClick={handlePlay} style={{
      margin: 0,
      left: 20,
      top: 20,
      right: 'auto',
      bottom: 'auto',
      position: 'fixed'
    }}>
      {start ? <PauseIcon/> : <PlayArrowIcon/>}
    </Fab>
    <WebPageContainer spacing={2}>
      <Typography>Model BY: miHoYo/观海子, Motion By: Nivsha</Typography>
      <FeedbackWidget resourceId={widgetId}/>
    </WebPageContainer>
    {/*<button onClick={handlePlay} style={{position: "absolute"}}>{start ? "Stop" : "Start"}</button>*/}

  </>;
};

export default ThreeModel;